<template>
  <div class="course-throw-screen">
    <head-top title="投屏演示">
      <span slot="right" class="head-right" @click="toThrowScreen">
       <span class="head_span">{{throwScreenInfo ? '停止演示' : '更新演示'}} </span>
      </span>
    </head-top>

    <div v-if="activeIndex===0">
      <div class="sub_nav">
        <span>成员列表</span>
      </div>
      <div class="sub_module">
        <span v-if="studentList.length===0">暂无数据</span>

        <van-checkbox-group v-else v-model="studentChecked" class="flex-wrap-between">
          <div v-for="(sub, k) in studentList" :key="k" class="student_card van-hairline--surround"
               @click="studentCheck(sub)">
            <van-checkbox sharp="square" :name="sub.studentID" class="student_check"></van-checkbox>
            <img :src="sub.imgSrc" class="col_4" alt=""/>
            <div>{{sub.studentName}}</div>
          </div>
          <div class="student_card" style="width: 19vw;" v-for="d in (4 - studentList.length % 4) % 4" :key="-d">
          </div>
        </van-checkbox-group>
      </div>

      <div class="sub_nav">
        <span>小组网关</span>
      </div>

      <div v-if="gatewayList && gatewayList.length>0" class="prepare-list">
        <van-checkbox-group v-model="gatewayChecked" direction="horizontal" class="sub_module">
          <van-checkbox shape="square" v-for="(item, index) in gatewayList" :key="index" :name="item.value">
            {{item.text}}
          </van-checkbox>
        </van-checkbox-group>

        <van-checkbox-group v-model="gatewayChecked" class="cell_group">
          <van-cell-group>
            <van-cell class="cell_main" v-for="(item, index) in gatewayList" :key="index" clickable
                      @click="gatewayCheck(item)">
              <template #icon>
                <van-icon class="line_icon" class-prefix="iconfont icon-wangguan" :style="{color:'#1989fa'}"></van-icon>
              </template>
              <template #title>
                <span class="line_title">{{item.text}}</span>
              </template>
              <van-checkbox slot="right-icon" sharp="square" :name="item.value"></van-checkbox>
            </van-cell>
          </van-cell-group>
        </van-checkbox-group>
      </div>
      <r-lack-data v-else></r-lack-data>

      <!--    <van-radio-group v-model="gatewayChecked" style="text-align: left;" @change="changeGatewayScreen"-->
      <!--                     :disabled="broadcastIP.length>0">-->
      <!--      <van-cell-group>-->
      <!--        <van-cell v-for="(item, index) in gatewayList" :key="index" :title="item.text" clickable-->
      <!--                  @click="gatewayScreenSelected=item.value">-->
      <!--          <van-radio slot="right-icon" :name="item.value"></van-radio>-->
      <!--        </van-cell>-->
      <!--      </van-cell-group>-->
      <!--    </van-radio-group>-->
    </div>
    <div v-else>
      <div class="sub_nav">
        <span>成员列表</span>
      </div>
      <van-tree-select :items="studentItems" :main-active-index.sync="studentIndex" :active-id="treeIndex" :max="5"
                       @click-nav="showTreeNode" @click-item="checkedTreeNode" :style="{height:'auto'}">
      </van-tree-select>
      <div class="sub_nav">
        <span>小组网关</span>
      </div>
      <van-tree-select :items="gatewayItems" :main-active-index.sync="gatewayIndex" :active-id="treeIndex" :max="5"
                       @click-nav="showTreeNode" @click-item="checkedTreeNode" :style="{height:'auto'}">
      </van-tree-select>
    </div>

    <div class="footer">
      <van-tabbar v-model="activeIndex">
        <van-tabbar-item v-for="(item, index) in menus" :key="index" @click="handleClick(item)">
          <i slot="icon" class="iconfont" :class="item.icon"></i>
          <span>{{item.name}}</span>
        </van-tabbar-item>
      </van-tabbar>
    </div>

  </div>
</template>

<script>
import { Dialog } from "vant";
import defaultImg from "../../../../assets/boy_img.png";

export default {
  name: "throwScreen",
  data () {
    return {
      throwScreenInfo: null,
      activeIndex: 0,
      menus: [
        { name: "全班演示", index: 0, icon: "icon-renmai" },
        { name: "定向演示", index: 1, icon: "icon-zuzhi" }
      ],
      studentList: [],
      studentChecked: [],
      gatewayList: [
        // { text: "分组1", value: "3" },
        // { text: "分组2", value: "4" }
      ],
      gatewayChecked: [],

      treeIndex: [1, 2],
      studentIndex: 0,
      studentItems: [
        {
          text: '学生1',
          badge: 2,
          children: [
            { text: '老师屏幕', id: 1 },
            { text: '小组1', id: 2 },
            { text: '小组2', id: 3, disabled: true },
            { text: '小组3', id: 4 }
          ]
        },
        {
          text: '学生2',
          badge: 1,
          children: [
            { text: '老师屏幕', id: 1 },
            { text: '小组1', id: 2 },
            { text: '小组2', id: 3, disabled: true },
            { text: '小组3', id: 4 }
          ]
        },
        {
          text: '学生3',
          badge: null,
          children: [
            { text: '老师屏幕', id: 1 },
            { text: '小组1', id: 2 },
            { text: '小组2', id: 3, disabled: true },
            { text: '小组3', id: 4 }
          ]
        },
        { text: '学生4', badge: null, children: [] },
        { text: '学生5', badge: null, children: [] }
      ],
      gatewayIndex: 0,
      gatewayItems: [
        {
          text: '小组1',
          badge: 2,
          children: [
            { text: '老师屏幕', id: 1 },
            // { text: '小组1', id: 2 },
            { text: '小组2', id: 3, disabled: true },
            { text: '小组3', id: 4 }
          ]
        },
        {
          text: '小组2',
          badge: null,
          children: [
            { text: '老师屏幕', id: 1 },
            { text: '小组1', id: 2 },
            { text: '小组3', id: 4 }
          ]
        },
        { text: '小组3', badge: null, children: [] }
      ]
    }
      ;
  },
  mounted () {
    this.getStudent();
    this.getGatewayList();
  },
  methods: {
    getStudent () {
      this.studentList = [];
      this.$gatewayHttp.student.getDataList().then(res => {
        if (res.Result && res.Datas) {
          res.Datas.forEach(item => {
            this.studentList.push({
              imgSrc: defaultImg,
              studentID: item["StudentID"],
              studentName: item["StudentName"],
              studentSno: item["StudentXH"]
              // studentSex: item["StudentXH"]
            });
          });
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getGatewayList () {
      this.gatewayList = [];
      this.$gatewayHttp.broadcast.getGatewayList().then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              this.gatewayList.push({
                text: item["GatewayName"],
                value: item["GatewayID"],
                IP: item["GatewayIP"],
                state: item["State"],
                online: item["IsOnline"]
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    studentCheck (item) {
      const index = this.studentChecked.findIndex(r => r === item.studentID);
      if (index > -1) {
        this.studentChecked.splice(index, 1);
      } else {
        this.studentChecked.push(item.studentID);
      }
    },
    gatewayCheck (item) {
      const index = this.gatewayChecked.findIndex(r => r === item.value);
      if (index > -1) {
        this.gatewayChecked.splice(index, 1);
      } else {
        this.gatewayChecked.push(item.value);
      }
    },
    toThrowScreen () {
      if (this.throwScreenInfo) {
        Dialog.confirm({
          message: "确认停止演示？"
        }).then(() => {
          this.stopThrowScreen();
        }).catch(() => {
        });
      } else {
        Dialog.confirm({
          message: "确认更新演示？"
        }).then(() => {
          this.startThrowScreen();
        }).catch(() => {
        });
      }
    },
    startThrowScreen () {

    },
    stopThrowScreen () {

    },

    handleClick (item) {

    },

    showTreeNode (value) {
      console.log("showTreeNode:", value);
    },
    checkedTreeNode (item) {
      console.log("checkedTreeNode:", item);
      const index = this.treeIndex.findIndex(r => r === item.id);
      if (index > -1) {
        this.treeIndex.splice(index, 1);
      } else {
        this.treeIndex.push(item.id);
      }
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-throw-screen {
  background-color: #fff;
  min-height: 100%;

  .active {
    border: 1px solid #ee0a24;
  }

  .student_check {
    position: absolute;
    top: 0;
    right: 0;
  }

  /deep/ .van-tree-select__item {
    font-weight: 500;
  }

  /deep/ .van-tree-select__item--active {
    color: #1989fa;
  }
}
</style>
